<template>
  <v-container>
    <v-row justify="center" class="bu">
      <v-btn class="mx-2" fab dark>
        10
      </v-btn>
    </v-row>
    <v-row justify="center">
      <v-col
        cols="12"
        justify-self="center"
        md="4"
        v-for="(item, n) in items"
        :key="n"
      >
        <v-row justify="center">
          <v-card max-width="200px" max-height="400px" color="purlple">
            <v-img
              class="white--text align-end"
              height="auto"
              width="auto"
              :src="item.fondo"
            ></v-img>
            <v-card-actions>
              <v-btn
                class="ma-2"
                color="dark"
                @click="agregarPlantilla(item.componente)"
                >{{ item.title }}</v-btn
              >
            </v-card-actions>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
    <v-row justify="center" style="padding-right:65px; padding-left:65px">
      <v-col md="12">
        <v-row>
          <v-btn dark @click="siguiente(false)" style="margin-right:10px"
            >Regresar</v-btn
          >
          <v-btn v-if="plantilla !== ''" dark @click="(dialog = true), RegistrarCV()">
            Crear
          </v-btn>

          <v-dialog v-model="dialog" max-width="290">
            <v-card>
              <v-card-title class="headline">Tu CV a sido creado!</v-card-title>

              <v-card-text>
                Da click en Aceptar para dirigirte a tu Perfil de Usuario.
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn
                  color="black"
                  class="white--text"
                  @click="RegistrarCV()"
                  style="margin-right:10px"
                >
                  <router-link
                    class="nav-item nav-link js-scroll-trigger"
                    style="color: white"
                    v-for="routes in links"
                    v-bind:key="routes.id"
                    :to="`${routes.page}`"
                    >{{ routes.text }}
                  </router-link>
                </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-row>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import CVS from "@/components/Dashboard_Usuario/PlantillaCV/CVS.vue";
import jsPDF from "jspdf";
const jwt = require("jsonwebtoken");
export default {
  data() {
    return {
      dialog: false,
      links: [
        {
          id: 0,
          text: "Aceptar",
          page: "/navegador_usuario"
        }
      ],
      campos: [],
      plantilla: "",
      items: [
        {
          title: "Basico",
          componente: "PLT0003",
          fondo: "https://i.ibb.co/VB4G67T/cv1.jpg"
        },
        {
          title: "Normal",
          componente: "PLT0002",
          fondo: "https://i.ibb.co/02yPBLP/cv2.jpg"
        },
        {
          title: "Premium",
          componente: "PLT0005",
          fondo: "https://i.ibb.co/6ZbHp3C/cv3.jpg"
        }
      ]
    };
  },
  created() {},

  methods: {
    RegistrarCV() {
      this.axios
        .post("/registroCV", this.$store.getters.RegistrarCV)
        .then(result => {
          console.log("Si llegó");
        })
        .catch(err => {
          console.log(err.response);
        });
    },
    siguiente(x) {
      this.$emit("siguiente", x);
    },
    agregarPlantilla(plantilla) {
      var plantilla_nombre = {
        fk_usuario: jwt.verify(this.$cookies.get("usuarioToken"), "mysecretkey").idUsuario,
        plantilla: plantilla
      };
      this.$store.dispatch("agregarPlantilla", plantilla_nombre);
      console.log(plantilla);
      this.plantilla = plantilla;
    }
  }
};



</script>
<style scoped>
@media only screen and (min-width: 1264px) and (max-width: 1904px) {
  .bu {
    display: none;
  }
}
@media only screen and (min-width: 960px) and (max-width: 1264px) {
  .bu {
    display: none;
  }
}
a {
  color: black;
}
</style>