<template>
  <v-container fluid>
     <v-row justify="center" class="bu">
      <v-btn class="mx-2" fab dark>
      4
    </v-btn>
    </v-row>
    <v-col md="12">
      <v-textarea
      v-model="perfil"
        :rules="rules"
        name="input-7-1"
        filled
        label="Descripción"
        auto-grow
        counter="250"
        maxlength="250"
        outlined
        height="200"
      ></v-textarea>
    </v-col>
    <v-col md="12">
      <p style="font-family: 'Jomolhari', serif;">
        Lorem ipsum dolor sit amet consectetur adipisicing elit.
        Reiciendis tempora alias aliquid quae beatae accusamus,
        sequi culpa labore ullam praesentium rem. Laborum sit esse
        eveniet aperiam illum. Sapiente, pariatur vel?
      </p>
    </v-col>
    <v-col md="12">
      <v-btn
        dark
        @click="siguiente(false)"
        style="margin-right:10px"
        >Regresar</v-btn
      >
      <v-btn
        dark
        @click="agregarPerfilProfesional(), siguiente(true)"
        style="margin-right:10px"
        >Siguiente</v-btn
      >
    </v-col>
  </v-container>
</template>

<script>
export default {
  data() {
    return {
      rules: [v => v.length <= 250 || "Sólo se permite 250 caracteres"],
      perfil:''
    };
  },
  methods:{
    agregarPerfilProfesional() {
      var pp = {
        perfil : this.perfil
      };

      this.$store.dispatch("agregarPerfilProfesional",pp);
      
    
    },
    siguiente(x){
      this.$emit('siguiente',x)
    }

  }
};
</script>
<style scoped>
@media only screen and (min-width: 1264px) and (max-width:1904px){
.bu{
  display: none;
}
}
@media only screen and (min-width: 960px) and (max-width:1264px){
.bu{
  display: none;
}
}
</style>