<template>
  <div class="modal-body">
    <!-- Default form register -->
    <form @submit.prevent="agregarUsuaruio()" class="text-center">
      <h1 style="font-family:'Montserrat', sans-serif;">Registrate</h1>
      <v-row>
        <v-col md="12" cols="12">
          <input
            type="text"
            v-model="usuario.nombre"
            class="form-control"
            placeholder="Nombre"
            required
          />
        </v-col>
        <v-col md="12" cols="12">
          <v-row justify="center">
            <v-col md="6" cols="12">
              <!-- Nombre(s) -->
              <input
                type="text"
                v-model="usuario.apellido_p"
                class="form-control"
                placeholder="Apellido Paterno"
                required
              />
            </v-col>

            <v-col md="6" cols="12">
              <input required
                type="text"
                v-model="usuario.apellido_m"
                class="form-control"
                placeholder="Apellido Materno"
              />
            </v-col>
          </v-row>
        </v-col>
        <v-col md="12" cols="12" style="margin-top:-2%">
          <input
            required
            type="email"
            v-model="usuario.correo"
            class="form-control"
            placeholder="Correo electrónico"
          />
        </v-col>

        <v-col
        v-if="correoExiste === false"
          cols="12"
          md="12"
          justify-self="start"
          class="pl-3 pt-2"
          style="margin-bottom:-25px;margin-top:-10px;"
        >
          <p class="text-danger">Correo electrónico no disponoble</p>
        </v-col>

        
        
        <v-col md="12" cols="12">
          <input
          required
            type="password"
            v-model="usuario.pass"
            class="form-control"
            placeholder="Contraseña"
            aria-describedby="defaultRegisterFormPasswordHelpBlock"
          />
        </v-col>
        <v-col
        v-if="passSize === false"
          cols="12"
          md="12"
          justify-self="start"
          class="pl-3 pt-2"
          style="margin-bottom:-25px;margin-top:-10px;"
        >
          <p class="text-danger">La contraseña debe tener al menos 6 caracteres</p>
        </v-col>
        <v-col md="12" cols="12">
          <input
          required
          v-model="pass2"
            type="password"
            id="defaultRegisterFormPassword"
            class="form-control"
            placeholder="Confirmar contraseña"
            aria-describedby="defaultRegisterFormPasswordHelpBlock"
          />
        </v-col>
        <v-col
        v-if="errorPassword"
          cols="12"
          md="12"
          justify-self="start"
          class="pl-3 pt-2"
          style="margin-bottom:-25px;margin-top:-10px;"
        >
          <p class="text-danger">La contraseña no coincide</p>
        </v-col>
      </v-row>

      <!-- Botón de Aceptar el Registro -->
      <button class="btn btn-primary my-4 btn-block" type="submit">Aceptar</button>
      <!-- Terms of service -->
      <p>
        ¿Ya tienes cuenta?
        <a>iniciar Sesión</a>
      </p>
      <hr />
      <small>
        Al registrarte aceptas los
        <a
          class="text-primary"
          href="#"
          target="_blank"
        >terminos de uso y condiciones de servicio</a> de Factor Humano
      </small>
    </form>
  </div>
</template>
<script>
export default {
  name: "RegistroUsuario",
  data() {
    return {
      correoExiste: true,
      pass2:"",
      passSize: true,
      errorPassword:false,
      usuario: {
        nombre: "",
        apellido_p: "",
        apellido_m: "",
        correo: "",
        pass: ""
      }
    };
  },

  methods: {

    matches(campo1,campo2){
      if(campo1 !== campo2){
        
        this.errorPassword = true
        return false
      }else{
       this.errorPassword = false
        return true
       
      }
    },
    sizePass(x){
      
      if(x.length<6){
        this.passSize = false
        console.log(x.length);
        
        return false
      }
      else{
        this.passSize = true
        console.log(x.length);
        return true
      }
    },

    agregarUsuaruio() {

      if(this.matches(this.pass2,this.usuario.pass) & this.sizePass(this.usuario.pass)  ){
        this.axios
        .post("/registro", this.usuario)
        .then(result => {
          this.correoExiste = true
          this.inicio(true);
          //this.inicio(true);
          //location.reload(false);
          
        })
        .catch(err => {
          console.log(err);
          this.correoExiste = false
        });
      }
      
    },
    inicio(x) {
      console.log("inicio");
      this.$emit("inicio", x);
    }
  }
};
</script>