<template>
  <div class="modal-body">
    <!-- Default form register -->
    <form
      @submit.prevent="login()"
      class="text-center pt-5 pl-3 pr-3"
      action="#!"
    >
    <h1 style="font-family:'Montserrat', sans-serif;">Iniciar Sesion</h1><br><br><br>
      <!-- E-mail -->
      <input
        type="email"
        id="defaultRegisterFormEmail"
        style="margin-top:-15%"
        class="form-control mb-4"
        placeholder="Correo electrónico"
        required
        v-model="usuario.correo"
      />
      <!-- Password -->
      <input
        type="password"
        id="defaultRegisterFormPassword"
        class="form-control mb-4"
        placeholder="Contraseña"
        style="margin-bottom:-20px;margin-top:-10px;"
        required
        v-model="usuario.pass"
      />

      <!-- Botón de Inicio de Sesión -->
      <v-row
        justify="start"
        class="pl-3"
        style="margin-bottom:-20px;margin-top:-20px;"
      >
        <label> <input type="checkbox" /> Recuérdame </label>
      </v-row>
      <v-row
        v-if="errorUsuario !== true"
        justify="center"
        class="pl-3 pt-2"
        style="margin-bottom:-25px;margin-top:10px;"
      >
        <p class="text-danger">Usuario y/o contraseña incorrectos</p>
      </v-row>

      <button class="btn btn-primary my-4 btn-block" type="submit">
        Iniciar Sesión
      </button>
      <!-- Terms of service -->
      <p style="margin-top:-15px;margin-bottom:-15px;">
        <a href target="#">Olvidé mi contraseña</a>
      </p>
    </form>
  </div>
</template>

<script>
const jwt = require("jsonwebtoken");
export default {
  name: "Login",
  data() {
    return {
      errorUsuario: true,
      usuario: {
        correo: "",
        pass: ""
      }
    };
  },
  created() {},
  watch(){},
  methods: {
    login() {
      // this.$store.dispatch("login", this.usuario);
      this.axios
        .post("/login", this.usuario)
        .then(result => {
          const token = result.data;
          this.errorUsuario = true;
          this.$cookies.set("usuarioToken", token,null, null, "localhost ");
          console.log(this.$cookies.get("usuarioToken"));
          
          
          //location.reload(false);
          this.inicio(true);
        })
        .catch(err => {
          console.log(err);
          this.errorUsuario = false;
        });
    },

    inicio(x) {
      console.log("inicio");
      this.$emit("inicio", x);
    }
  }
};
</script>