<template>
  <div id="app">
    <v-card class="ma-3 pa-6" outlined tile max-width="800">
      <v-row align="center" justify="center">
        Escoje una de las opciones para tus CV
      </v-row>
      <hr/>
      <v-row v-show="plantilla === false">
        <v-col cols="6" sm="6" md="6" v-for="(cv, n) in cvs" :key="n">
          <v-card class="mx-auto" max-width="500" color="purlple">
            <v-img :src="imagen(cv.Plantilla)" class="white--text align-end" height="auto" width="auto">
            </v-img>
            <v-card-actions>
              <v-btn
                @click="cambiarComp(cv.Plantilla), (plantilla = true)"
              >
              {{cv.Plantilla}}
              </v-btn>
            </v-card-actions>
          </v-card>
        </v-col>
      </v-row>
      <br />
    </v-card>
    <v-content>
      <v-container fluid v-if="plantilla === true">
        <v-btn light @click="plantilla = false">Regresar</v-btn>
        <keep-alive>
          <component v-bind:is="componenteA"></component>
        </keep-alive>
      </v-container>
    </v-content>
  </div>
</template>

<script>
import PLT0003 from "@/components/Dashboard_Usuario/PlantillaCV/CV_Usuario.vue";
import PLT0002 from "@/components/Dashboard_Usuario/PlantillaCV/CV_Usuario2.vue";
import PLT0005 from "@/components/Dashboard_Usuario/PlantillaCV/CV_Usuario3.vue";
import CVS from "@/components/Dashboard_Usuario/PlantillaCV/CVS";
const jwt = require("jsonwebtoken");
export default {

  components: {
    PLT0003,
    PLT0002,
    PLT0005,
  },


  data() {
    return {
      cvs: [],
      cvID:'',
      plantilla: false,
      drawer: true,
      componenteA: "",
      items: [
        {
          title: "Curriculum 1",
          componente: "CV_Usuario",
          fondo: "https://i.ibb.co/hL5X5Fr/Sin-t-tulo-1.jpg"
        },
        {
          title: "Curriculum 2",
          componente: "CV_Usuario2",
          fondo: "https://i.ibb.co/p3sVg5j/Sin-t-tulo-2.jpg"
        },
        {
          title: "Curriculum 3",
          componente: "CV_Usuario3",
          fondo: "https://i.ibb.co/Qr82tbM/Sin-t-tulo-4.jpg"
        }
      ]
    };
  },
  created: function() {
    this.cargarCVs();
  },
  methods: {
    imagen(id){
    
      let img=''
      switch (id) {
        case "PLT0003":
          img ='https://i.ibb.co/VB4G67T/cv1.jpg'
          break;
        case "PLT0002":
          img= 'https://i.ibb.co/02yPBLP/cv2.jpg'
          break;
        case "PLT0005":
          img = 'https://i.ibb.co/6ZbHp3C/cv3.jpg'
          break;
        default:
          break;
      }
      console.log(id);
      
      console.log(img,"Imagen");
      
      return img
      
    },
    cambiarComp(componente) {
      this.cvID = componente
      this.componenteA = componente;
      console.log(this.componenteA);
    },
    cargarCVs() {
      //const id= jwt.verify(this.$cookies.get("usuarioToken"), 'mysecretkey');
      let usuarioID = jwt.verify(
        this.$cookies.get("usuarioToken"),
        "mysecretkey"
      );
      console.log("Se envía el id: ", usuarioID.idUsuario);

      this.axios
        .post("/usuario_cvs", {
          userID: {
            idChido: usuarioID.idUsuario
          }
        })
        .then(result => {
          
          result.data.forEach(cv => {
            this.cvs.push(cv)
          });
          console.log("CVS",this.cvs[0].Plantilla);
        })
        .catch(err => {
          console.log(err);
          console.log("Mal");
        });
    },
    enviarInfoCV(cvID){
      this.$emit("cv", );
    }
  }
};
</script>

<style scoped>
.mx-auto {
  -webkit-box-shadow: 0px -1px 0px 3px rgba(0, 0, 0, 1);
  -moz-box-shadow: 0px -1px 0px 3px rgba(0, 0, 0, 1);
  box-shadow: 0px -1px 0px 3px rgba(0, 0, 0, 1);
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active in <2.1.8 */ {
  opacity: 0;
}
</style>